    }
    .zhibiao{
        display: inline-block;
        position: absolute;
        right: 10px;
    }
    .zhibiao span{

        height: 24px;
        text-align: right;
        font-size: 16px;
        font-weight: 600;
        color: #fc5531;
        line-height: 24px;
        position: relative;
        padding-right: 3px;
    }
    .zhibiao p{
        font-size: 12px;
        color: #999;
        line-height: 17px;
        text-align: right;
    }
    .inner-bou{
        height: 1000px;
    }

    .boxtaolun{
        margin-top: 10px;
    }
    .boxtaolun:hover{
        background-color: #f5f9f5;
    }
    .taolunName{
        margin-left: 10px;
        position: absolute;
    }
    .taolunName:hover{
        color: #62b038;
    }
    .taolun{
        font-size: 12px;
        color: rgba(45,181,93);
        background: rgba(45,181,93,0.15);
