    }

    .clearfix:after {
        clear: both
    }

    .box-card {
        width: 791px;

    }
    .head2{
        font-size: 16px;
        line-height: 22px;
        font-weight: 500;

        overflow: hidden;
        text-overflow: ellipsis;
        white-space: pre;
        margin-right: 10px;
        flex: 1 1 0%;
    }
    .inner-bou div{
        cursor: pointer;

    }
    .inner-bou .box{
        border-top: 1px solid rgba(240,240,240, 1);
    }
    .inner-bou .dec{
        font-size: 14px;
        line-height: 20px;
        color: rgba(60,60,67,0.6);
        -webkit-line-clamp: 2;
        overflow: hidden;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        margin: 16px 0px 18px;
    }

</style>